import React, { useState } from "react";
import "./index.css"



function App() {
    const [inputVal , setinputVal] = useState("0")
    const [operator , setOperator] = useState("")
    const [perv , setPerv] = useState("0")
  return (
    <div className="app">
          <div className="inpt">
                <input type="number/text"  id="inp" value={inputVal} readOnly={true}/>
          </div>
          <section className="btns">
            <NumButton number={1}/>
            <NumButton number={2}/>
            <NumButton number={3}/>
            <NumButton number={4}/>
            <NumButton number={5}/>
            <NumButton number={6}/>
            <NumButton number={7}/>
            <NumButton number={8}/>
            <NumButton number={9}/>
            <NumButton number={0}/>
            <ClearButton/>
            <NumButton number={"."}/>
            <PlusButton/>
            <MinusButton/>
            <TimesButton/>
            <DivideButton/>
            <EqualButton/>
          </section>
    </div>
  )

  function NumButton(probs) {
      var dot_count = 0
    function clicked() {
        for(var number of inputVal ){
            if(number === "."){ dot_count++}
        }

        if(inputVal === "0"){
            if(probs.number === "."){
                console.log("sot")
                if(dot_count < 1){
                setinputVal( String(inputVal + ".") )
            }

            }
            else{
                if(probs.number === "."){
                    if(dot_count < 1){
                    setinputVal( String(inputVal + ".") )
                    }  
                }
                  
                else{setinputVal(String(probs.number))}
            }
        }
        else{
            if(probs.number === "."){
                    if(dot_count < 1){
                    setinputVal( String(inputVal + ".") )
                    }  
                }
            else{
            setinputVal(inputVal + probs.number)}
        }
    }

    

    return (
        <button className="btn" onClick={clicked}>
            {probs.number}
        </button>
    )
}

function ClearButton(){
    
    const Clear = () =>{
        setPerv("0")
        setinputVal("0")
    }

    return(
        <button className="btn" id="clearbtn" onClick={Clear}>
            C
        </button>
    )
}

function PlusButton(){
    
    const doplus = () => {
        if(perv === "0"){
        setPerv(inputVal)
        setinputVal("0")
        setOperator("+")}
        else{
            setPerv(String(parseFloat(perv) + parseFloat(inputVal)))
            setinputVal("0")
        }
    }

    return(
        <button className="btn" onClick={doplus}> + </button>
    )
}


function TimesButton(){

    function doTimes(){
        if(perv === "0"){
        setPerv(inputVal)
        setinputVal("0")
        setOperator("*")}
        else{
            setPerv(String(parseFloat(perv) * parseFloat(inputVal)))
            setinputVal("0")
        }
    }


    return(
        <button className="btn" onClick={doTimes}> * </button>
    )
}

function DivideButton(){
    function doTimes(){
        if(perv === "0"){
        setPerv(inputVal)
        setinputVal("0")
        setOperator("/")}
        else{
            setPerv(String(parseFloat(perv) / parseFloat(inputVal)))
            setinputVal("0")
        }
    }


    return(
        <button className="btn" onClick={doTimes}> / </button>
    )
}

function MinusButton(){
    function doMinus(){
        if(perv === "0"){
        setPerv(inputVal)
        setinputVal("0")
        setOperator("-")}
        else{
            setPerv(String(parseFloat(perv) - parseFloat(inputVal)))
            setinputVal("0")
        }
    }


    return(
        <button className="btn" onClick={doMinus}> - </button>
    )
}

function EqualButton(){
    function __eq__(){
        if(operator === "+"){
            var newVal = String(parseFloat(perv) + parseFloat(inputVal))
            setinputVal(newVal)
        }

        else if(operator === "*"){
            var newVal = String(parseFloat(perv) * parseFloat(inputVal))
            setinputVal(newVal)
        }
        else if(operator === "/"){
            var newVal = String(parseFloat(perv) / parseFloat(inputVal))
            setinputVal(newVal)
        }
        else if(operator === "-"){
            var newVal = String(parseFloat(perv) - parseFloat(inputVal))
            setinputVal(newVal)
        }
    }

    return(
        <button className="btn" onClick={__eq__} id="eq_btn"> = </button>
    )
}




}
export default App;
